* {
  box-sizing: border-box;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.allCards {
  padding: 10px;
  margin-bottom: 10px;
  border: none;
  text-align: center;
}

.allCards img {
  height: 200px;
  width: 200px;
  background-color: transparent;
}
.mainContainer {
  padding: 0;
  background-color: #BACDD4;
}

.instructions {
  text-align: center;
  max-width: 100%;
  align-content: center;
}

.instructions p {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  background-color: #65516d;
  color: white;
  padding: 20px 0;
}

.jumbotron {
	background-image: url("https://images.unsplash.com/photo-1480365334925-2aee561aa28e?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1373&q=80");
    background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	margin-bottom: 0px;
	text-align: center;
	padding: 7%;
}

.jumbotron h1 {
	font-family: 'Times New Roman', Times, serif;
	font-size: 65px;
	font-weight: 900;
	color:  white;
}


